<template>
  <div class="falllist">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>跌倒危險因子評估表</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd
                :viewData="viewData"
                @getAllData="getAllData"
                ref="editProcess"
              ></popupadd>
              <MultiPrintSelect :printChoice="printChoice" :printItems="printItems" @changeChoice="changeChoice"></MultiPrintSelect>

            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead style="background-color: #e3f2fd;">
                      <tr>
                        <th rowspan="3"></th>
                        <th rowspan="2">評估日期</th>
                        <th colspan="14">跌倒危險因子評估表</th>
                        <th rowspan="2" class="ced_man_key">紀錄人</th>
                        <th rowspan="2" class="ced_man_key">修改紀錄</th>
                      </tr>
                      <tr>
                        <th>1.年齡</th>
                        <th>2.跌倒病史</th>
                        <th>3.活動能力</th>
                        <th>4.意識狀態</th>
                        <th>5.行為</th>
                        <th>6.夜間頻尿</th>
                        <th>7.視覺障礙</th>
                        <th>8.步態平衡</th>
                        <th>9.藥物</th>
                        <th>內容</th>
                        <th>10.新入住或新轉入</th>
                        <th>11.衣著(含鞋子)</th>
                        <th>12.環境中熟悉物件</th>
                        <th>總分</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td class="d-flex align-center">
                          <v-checkbox label="列印選擇" v-model="item.check" v-if="!printChoice" @click.stop="filterPrintItem"></v-checkbox>
                          <v-menu transition="scale-transition" offset-y v-if="printChoice">
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.age}}</td>
                        <td>{{item.history}}</td>
                        <td>{{item.active}}</td>
                        <td>{{item.conscious}}</td>
                        <td>{{item.behavior}}</td>
                        <td>{{item.pee_rate}}</td>
                        <td>{{item.visual_impairment}}</td>
                        <td>{{item.gait_balance}}</td>
                        <td>{{item.medicine}}</td>
                        <td>{{item.medicine_content}}</td>
                        <td>{{item.new_in}}</td>
                        <td>{{item.clothes}}</td>
                        <td>{{item.familiar_object}}</td>
                        <td>{{item.total}}</td>
                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import MultiPrintSelect from "@/components/MultiPrintSelect";
import popupadd from "@/views/document/FallAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd ,MultiPrintSelect },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      printChoice: true, //設定列印選擇
      printItems:[],
    };
  },
  mounted() {
    this.getAllData();
  },
  methods: {
    //切換顯示功能
    changeChoice(){
      this.printChoice = !this.printChoice;
      this.items.filter(i=>{
        i.check = "";
      })
      this.printItems = [];
    },
    //判斷選擇準備列印的內容
    filterPrintItem(){
      if (this.printItems.length == 5){
        var pop = { msg: "單次列印指定數最多5筆!!", type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
      }
      this.printItems = this.items.filter(i=>{
        return i.check;
      })
    },
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" + this.$store.state.databaseName + "/fall",
          qs.stringify(postData)
        )
        .then(rs => {
         this.items = this.viewData = rs.data.map(i => {
            var newItem = JSON.parse(i.datalist);
            newItem.snkey = i.snkey;
            newItem.check = false;
            return newItem;
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依name排序
      this.viewData = this.viewData.sort(function(a, b) {
        return parseInt(a.date) < parseInt(b.date) ? 1 : -1;
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent",item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        if (res) {
          this.$store.commit("getTime"); //取得現在時間
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "fall",
            info: JSON.stringify(delData)
          };

          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/fall",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    }
  }
};
</script>


<style lang="scss">
.falllist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>