<template>
  <div class="adaptadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <table class="table table-bordered table-sm bg-light">
              <tr>
                <td class="form_title align-middle" colspan="4">評估日期</td>
                <td>
                  <v-text-field type="date" v-model="list.date" :rules="emptyRules"></v-text-field>
                </td>
              </tr>
              <!-- 跌倒危險因子評估表 -->
              <tr>
                <td class="form_title" rowspan="13">
                  <span class="vertical-mode">跌倒危險因子評估表</span>
                </td>
                <td>1</td>
                <td>年齡</td>
                <td>0.小於65歲 1.大於65歲</td>
                <td>
                  <select class="form-control" id="age" name="age" v-model="list.age">
                    <option></option>
                    <option value="0">0.小於65歲</option>
                    <option value="1">1.大於65歲</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>2</td>
                <td>跌倒病史</td>
                <td>0.無 5.有跌倒病史</td>
                <td>
                  <select class="form-control" id="history" name="history" v-model="list.history">
                    <option></option>
                    <option value="0">0.無</option>
                    <option value="5">5.有跌倒病史</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>3</td>
                <td>活動能力</td>
                <td>0.無活動功能障礙,不需要他人、輔助器協助 1.有活動功能障礙,需他人、輔助器協助</td>
                <td>
                  <select class="form-control" id="active" name="active" v-model="list.active">
                    <option></option>
                    <option value="0">0.無活動功能障礙,不需要他人、輔助器協助</option>
                    <option value="1">1.有活動功能障礙,需他人、輔助器協助</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>4</td>
                <td>意識狀態</td>
                <td>0.清醒有定向感 1.人、時、地混淆</td>
                <td>
                  <select
                    class="form-control"
                    id="conscious"
                    name="conscious"
                    v-model="list.conscious"
                  >
                    <option></option>
                    <option value="0">0.清醒有定向感</option>
                    <option value="1">1.人、時、地混淆</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>5</td>
                <td>行為</td>
                <td>0.正常 1.躁動不安 1.沮喪</td>
                <td>
                  <select
                    class="form-control"
                    id="behavior"
                    name="behavior"
                    v-model="list.behavior"
                  >
                    <option></option>
                    <option value="0">0.正常</option>
                    <option value="1">1.躁動不安</option>
                    <option value="1">1.沮喪</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>6</td>
                <td>夜間頻尿</td>
                <td>0.否 1.是</td>
                <td>
                  <select
                    class="form-control"
                    id="pee_rate"
                    name="pee_rate"
                    v-model="list.pee_rate"
                  >
                    <option></option>
                    <option value="0">0.否</option>
                    <option value="1">1.是</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>7</td>
                <td>視覺障礙</td>
                <td>0.否 1.有</td>
                <td>
                  <select
                    class="form-control"
                    id="visual_impairment"
                    name="visual_impairment"
                    v-model="list.visual_impairment"
                  >
                    <option></option>
                    <option value="0">0.否</option>
                    <option value="1">1.有</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>8</td>
                <td>步態平衡</td>
                <td>0.步態穩健平衡 1.步態不穩健平衡</td>
                <td>
                  <select
                    class="form-control"
                    id="gait_balance"
                    name="gait_balance"
                    v-model="list.gait_balance"
                  >
                    <option></option>
                    <option value="0">0.步態穩健平衡</option>
                    <option value="1">1.步態不穩健平衡</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>9</td>
                <td>藥物</td>
                <td>
                  0.未使用以下藥物 2.服用以下二種藥物藥名
                  <br />由下藥物中,標示出目前使用藥物種類:
                  <br />1.抗組織胺 2.抗高血壓 3.鎮靜安眠藥 4.肌肉鬆弛劑 5.輕瀉劑 6.利尿劑 7.抗抑鬱藥 8.降血糖藥
                </td>
                <td>
                  <select
                    class="form-control"
                    id="medicine"
                    name="medicine"
                    v-model="list.medicine"
                  >
                    <option></option>
                    <option value="0">0.未使用以下藥物</option>
                    <option value="1">1.服用以下一種藥物藥名</option>
                    <option value="2">2.服用以下二種藥物藥名</option>
                  </select>
                  <input
                    v-if="list.medicine != 0"
                    type="text"
                    id="medicine_content"
                    name="medicine_content"
                    class="form-control"
                    v-model="list.medicine_content"
                    placeholder="內容"
                  />
                </td>
              </tr>
              <tr>
                <td>10</td>
                <td>新入住或新轉入</td>
                <td>0.否 1.是</td>
                <td>
                  <select class="form-control" id="new_in" name="new_in" v-model="list.new_in">
                    <option></option>
                    <option value="0">0.否</option>
                    <option value="1">1.是</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>11</td>
                <td>衣著(含鞋子)</td>
                <td>0.衣服、褲子大小合宜、鞋子防滑 1.衣服寬鬆、褲子太長、鞋子易滑</td>
                <td>
                  <select class="form-control" id="clothes" name="clothes" v-model="list.clothes">
                    <option></option>
                    <option value="0">0.衣服、褲子大小合宜、鞋子防滑</option>
                    <option value="1">1.衣服寬鬆、褲子太長、鞋子易滑</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td>12</td>
                <td>環境中熟悉物件</td>
                <td>0.擺放於健側或於熟悉位置 1.物件擺放被移動</td>
                <td>
                  <select
                    class="form-control"
                    id="familiar_object"
                    name="familiar_object"
                    v-model="list.familiar_object"
                  >
                    <option></option>
                    <option value="0">0.擺放於健側或於熟悉位置</option>
                    <option value="1">1.物件擺放被移動</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td colspan="2">
                  ≧4分表示有跌倒傾向
                  <small>出處:台灣長期照顧專業協會之機構品質指標監測操作手冊</small>
                </td>
                <td>總分</td>
                <td>
                  <input type="text" class="form-control text-danger" :value="total" readonly />
                </td>
              </tr>
            </table>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'" :loading="loading" :disabled="loading">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'" :loading="loading" :disabled="loading">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"],
      loading : false,
    };
  },
  computed: {
    total() {
      var t =
        parseInt(this.list.age) +
        parseInt(this.list.history) +
        parseInt(this.list.active) +
        parseInt(this.list.conscious) +
        parseInt(this.list.behavior) +
        parseInt(this.list.pee_rate) +
        parseInt(this.list.visual_impairment) +
        parseInt(this.list.gait_balance) +
        parseInt(this.list.medicine) +
        parseInt(this.list.new_in) +
        parseInt(this.list.clothes) +
        parseInt(this.list.familiar_object);
      this.list.total = t;
      return t;
    }
  },
  props: ["viewData"],
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      if (this.viewData[0] != undefined) {
        this.list = { ...this.viewData[0] };
      }

      this.list.snkey = "";
      this.list.date = "";
      this.list.edit_man = "";
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        var postData = {
          u_snkey: this.list.u_snkey,
          datalist: JSON.stringify(this.list),
          create_man: this.list.create_man
        };

        this.loading = true;
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/fall",
            qs.stringify(postData)
          )
          .then(rs => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "error" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning"
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        var postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: this.list.edit_man
        };

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/fall",
            qs.stringify(postData)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
              for (var i in this.list) {
                this.oldList[i] = this.list[i];
              }
              this.loading = false;
            }
          });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";

.form_title {
  font-size: 1.2rem;
  text-align: center;
}
.vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
</style>